import { Link } from "react-router-dom";
import "./Footer.css"
import Logo from "../Logo/Logo";
import { useState } from "react";

export default function Footer() {
  let [contacts,] = useState([
    { site: "vimeo", icon: "fa-vimeo", path: "https://vimeo.com/user150035662" },
    { site: "behance", icon: "fa-behance", path: "https://www.behance.net/abdelrhmanmikky" },
    { site: "instagram", icon: "fa-instagram", path: "https://www.instagram.com/abdelrhman.mekky" },
  ])

  return (
    <div className="footer">
      <Logo />
      <div className="contact-info">
        {
          contacts.map((contact, index) => (
            <div key={index} className={`${contact.site} flex-item`}>
              <Link to={contact.path}>
                <i className={`fa-brands ${contact.icon}`}></i>
              </Link>
            </div>
          ))
        }
      </div>
      <div className="copyrights">&copy;2023 | All rights reversed | Developed and powered by <Link to={"https://github.com/nourbeh302"}>Nourbeh</Link></div>
    </div>
  );
}
