import Navbar from "../Navbar/Navbar";
import "./Hero.css";

export default function Hero() {
    var scrollDown = () => window.scrollBy(0, window.innerHeight);
    
    return (
        <>
            <div className="Hero">
                <Navbar />
                <div className="hero-overlay">
                    <div className="hero-content">
                        <h1>I merge audio with <span>images</span></h1>
                        <p>Welcome to my website! I'm a passionate video editor specializing in creating captivating content that brings stories to life. Let's collaborate on your next project!</p>
                    </div>
                    <button className="cta-button" onClick={() => scrollDown()}>
                        <span>Scroll Down</span>
                    </button>
                </div>
            </div>
        </>
    )
}