import { useState } from "react";
import "./OctoberTuners.css"
import { process } from 'process';

const publicUrl = process.env.PUBLIC_URL;

export default function OctoberTuners() {

  var [images,] = useState([
    `${publicUrl}/images/october_tuners/_01.jpg`,
    `${publicUrl}/images/october_tuners/_02.jpg`,
    `${publicUrl}/images/october_tuners/_03.jpg`,
    `${publicUrl}/images/october_tuners/_04.jpg`,
  ])

  // October tunners -> Cars
  // Alexandria

  return (
    <div className="OctoberTuners" id="october-tuners">
      <div className="photos-overlay container">
        <h2>October Tuners</h2>
        <div className="images-container grid-container">
          {
            images.map((image, index) => (
              <div key={index} className="image-item">
                <img src={image} alt={image} />
              </div>
            ))
          }
        </div>
      </div>
    </div>
  );
}
