import { useState } from "react";
import "./Alex.css"
import { process } from 'process';

const publicUrl = process.env.PUBLIC_URL;

export default function Alex() {

  var [images,] = useState([
    { src: `${publicUrl}/images/alex/01.jpg`, isLarge: true },
    { src: `${publicUrl}/images/alex/05.jpg`, isLarge: false },
    { src: `${publicUrl}/images/alex/02.jpg`, isLarge: true },
    { src: `${publicUrl}/images/alex/06.jpg`, isLarge: false },
    { src: `${publicUrl}/images/alex/03.jpg`, isLarge: true },
    { src: `${publicUrl}/images/alex/04.jpg`, isLarge: true },
  ])

  return (
    <div className="Alex" id="alex">
      <div className="photos-overlay container">
        <h2>Alexandria</h2>
        <div className="images-container grid-container">
          {
            images.map((image, index) => (
              <div key={index} className={`image-item ${image.isLarge ? "large" : "normal"}`}>
                <img src={image.src} alt={image.src} />
              </div>
            ))
          }
        </div>
      </div>
    </div>
  );
}
